@import url('https://fonts.googleapis.com/css2?family=Calligraffitti&family=Caveat&family=Nunito:wght@200&family=Quicksand:wght@300;500&family=Roboto:wght@100&display=swap');

* {
    --quicksand: 'Quicksand';
    --caveat: 'Caveat';
    --nunito: 'Nunito';
    --blue: rgb(0, 0, 134);
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    min-width: 0;
}

html,
body {
    font-size: 15px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3.46667rem;
    scroll-behavior: smooth;
}

/* home */
main {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    max-width: 100%;
    min-height: 100vh;
    height: 100vh;
    align-items: center;
    justify-content: center;
}

#welcome {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.7vh;
}

#intro {
    color: #3C3EF1;
    font-family: var(--quicksand);
    font-size: 6.5vw;
    font-weight: 500;
}

#hi {
    color: #33404E;
    font-family: var(--quicksand);
    font-size: 3.5vw;
    font-weight: 600;
}

#talk {
    width: 65vw;
    color: #33404E;
    text-align: center;
    font-family: var(--nunito);
    font-size: 2.8vw;
    font-weight: 600;
    margin: 1.2vh 0;
}

#logos {
    display: flex;
    width: 70vw;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    align-content: center;
    gap: 0.5vw;
    flex-wrap: wrap;
}

images {
    width: 12vw;
    height: 6.5vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border-radius: 0.4rem;
    border: 1px solid #E7E7E7;
    background: #F9F9F9;
}

#aws {
    height: 2.5vh;
}

.htmlcss {
    width: 4vw;
}

.flaskgit {
    width: 7vw;
    height: 1.9vh;
}

images img {
    width: 5vw;
    height: 3vh;
    flex-shrink: 0;
    background-color: lightgray 50% / cover no-repeat;
    visibility: hidden;
    animation: show 2s linear forwards;
    animation-delay: calc(0.2s * var(--i));
}

@keyframes show {
    0% {
        visibility: visible;
        scale: 0.5;
    }

    25% {
        transform: translateY(-1vh);
    }

    50% {
        transform: translateY(1vh);
    }

    75% {
        scale: 1.2;
    }

    100% {
        visibility: visible;
        scale: 1.0;
    }
}

/* small screens */
@media (min-width: 0px) and (max-width: 321px) {

    #background {
        scale: 0.7;
    }

    #talk {
        font-size: 3.4vw;
    }

    #logos {
        display: flex;
    }

    images {
        display: flex;
        flex-wrap: wrap;
        width: 10vw;
        height: 6vh;
    }

    #aws {
        height: 2.5vh;
    }

    .htmlcss {
        width: 4vw;
    }

    .flaskgit {
        height: 2vh;
    }

    images img {
        width: 5vw;
        height: 3.8vh;
    }
}

/* tablets */
@media (min-width: 768px) and (max-width: 1199px) and (orientation: portrait) {

    /* home */
    images {
        width: 10vw;
        height: 8vh;
    }

    .flaskgit {
        height: 2.8vh;
    }

    images img {
        width: 5vw;
        height: 4vh;
    }
}

@media screen and (max-width: 1199.9px) and (orientation: landscape) {

    #intro {
        font-size: 6vh;
    }

    #hi {
        font-size: 3.5vh;
    }

    #talk {
        font-size: 3vh;
    }

    images {
        width: 18vh;
        height: 5vw;
    }

    #aws {
        width: 4vw;
        height: 6vh;
    }

    .htmlcss {
        width: 2.5vw;
    }

    .flaskgit {
        width: 5vw;
        height: 4vh;
    }

    images img {
        width: 3vw;
        height: 3vw;
    }
}

/* PC Screens */
@media screen and (min-width: 1200px) {

    /* home */
    #welcome {
        gap: 1.6vh;
    }

    #intro {
        font-size: 6.5vw;
    }

    #hi {
        font-size: 2vw;
    }

    #talk {
        font-size: 1.1vw;
    }

    #logos {
        width: 50vw;
    }

    images {
        width: 5vw;
        height: 8vh;
    }

    #aws {
        width: 3vw;
        height: 4vh;
    }

    .htmlcss {
        width: 2vw;
    }

    .flaskgit {
        width: 3.5vw;
        height: 2.8vh;
    }

    images img {
        width: 2vw;
        height: 4.5vh;
    }
}